import Header from '../../components/Header'
import Footer from '../../components/Footer'
import Navigation from '../../components/Navigation'
import React, { Component } from "react";
import Form from 'react-validation/build/form';
import Input from 'react-validation/build/input';
import CheckButton from 'react-validation/build/button';
import cust_login from '../../images/customer_portal.jpg'

import CustAuthService from '../../services/cust-auth-service';

const required = value => {
    if(!value) {
        return (
            <div className="alert alert-danger" role="alert">
                This field is required!
            </div>
        );
    }
};

export default class CustomerLoginScreen extends Component {
    constructor(props) {
        super(props);
        this.handleLogin = this.handleLogin.bind(this);
        this.onChangeUsername = this.onChangeUsername.bind(this);
        this.onChangePassword = this.onChangePassword.bind(this);
        this.onChangeDob = this.onChangeDob.bind(this);

        this.state = {
            username: "",
            password: "",
            dob: "",
            loading: false,
            message: ""
        }
    }

    onChangeUsername(e) {
        this.setState({
            username: e.target.value
        });
    }
    
    onChangePassword(e) {
        this.setState({
            password: e.target.value
        });
    }

    onChangeDob(e) {
        this.setState({
            dob: e.target.value
        });
    }
    
    handleLogin(e) {
        e.preventDefault();
    
        this.setState({
          message: "",
          loading: true
        });
    
        this.form.validateAll();
    
        if (this.checkBtn.context._errors.length === 0) {
          CustAuthService.login(this.state.username, this.state.password, this.state.dob).then(
            () => {
              this.props.history.push("/customer/profile");
              window.location.reload();
            },
            error => {
              const resMessage =
                (error.response &&
                  error.response.data &&
                  error.response.data.message) ||
                error.message ||
                error.toString();
    
              this.setState({
                loading: false,
                message: resMessage
              });
            }
          );
        } else {
          this.setState({
            loading: false
          });
        }
    }

    render () {
        return (
            <div>
            <Header/>
            <Navigation />
            <h1 className="display-6" id="customer_title">Customer Portal</h1>
            <section>
            <div className="row">
            <div class="col-md-6 gx-5 mb-4">
                <img src={cust_login} alt="customer_portal" id="cust_portal_img"></img>
            </div>
            <div class="col-md-6 gx-5 mb-4">
                <div className="col-md-12">
                    <div className="card card-container">
                    <img
                        src="//ssl.gstatic.com/accounts/ui/avatar_2x.png"
                        alt="profile-img"
                        className="profile-img-card"
                    />

                    <Form
                        onSubmit={this.handleLogin}
                        ref={c => {
                        this.form = c;
                        }}
                    >
                        <div className="form-group">
                        <label htmlFor="username">Email</label>
                        <Input
                            type="text"
                            className="form-control"
                            name="email"
                            value={this.state.username}
                            onChange={this.onChangeUsername}
                            validations={[required]}
                        />
                        </div>

                        <div className="form-group">
                        <label htmlFor="password">Password</label>
                            <Input
                                type="password"
                                className="form-control"
                                name="password"
                                value={this.state.password}
                                onChange={this.onChangePassword}
                                validations={[required]}
                            />
                        </div>

                        <div className="form-group">
                        <label htmlFor="dob">Date of Birth</label>
                        <Input
                            type="date"
                            className="form-control"
                            name="dob"
                            value={this.state.dob}
                            onChange={this.onChangeDob}
                            validations={[required]}
                        />
                        </div>

                        <div className="form-group">
                        <button
                            id="cust_btn"
                            className="btn btn-primary btn-block"
                            disabled={this.state.loading}
                        >
                            {this.state.loading && (
                            <span className="spinner-border spinner-border-sm"></span>
                            )}
                            <span>Login</span>
                        </button>
                        </div>

                        {this.state.message && (
                        <div className="form-group">
                            <div className="alert alert-danger" role="alert">
                            {this.state.message}
                            </div>
                        </div>
                        )}
                        <CheckButton
                        style={{ display: "none" }}
                        ref={c => {
                            this.checkBtn = c;
                        }}
                        />
                    </Form>
                    </div>
                </div>
            </div>
            </div>
            </section>
            <Footer />
        </div>
        );
    }
}
